import React from "react";
import { Card } from "react-bootstrap";
import Botol from "../images/botoll.png";

function ProdukCard(props) {
  return (
    <Card className="shadow">
      <div class="mx-auto pt-4">
        <Card.Img
          className="img-responsive card-img-top mx-auto rounded"
          variant="top"
          src={Botol}
          alt="Kopi Cold White Kafeins"
        />
      </div>
      <Card.Body className="text-center">
        <p class="open-sans text-24 font-weight-700 mb-1">{props.title}</p>
        <p class="roboto text-16 mb-1">{props.description}</p>
      </Card.Body>
    </Card>
  );
}

export default ProdukCard;
